
// Category Swiper
const categoryMainSwiper = new Swiper('.c-swiper', {
	loop: true,
	centeredSlides: true,
	watchSlidesVisibility: true,
	navigation: {
		nextEl: '.c-swiper-button-next',
		prevEl: '.c-swiper-button-prev',
	},

	autoplay: {
		delay: 10000,
		disableOnInteraction: false,
	},

	slidesPerColumn: 1,
	breakpoints: {// настройки для разных разрешений
		400: {
			slidesPerView: 1,
			spaceBetween: 20
		},
		767: {
			slidesPerView: 1.1,
			spaceBetween: 30
		},
		992: {
			slidesPerView: 1.5,
			spaceBetween: 40,
		},
		1200: {
			slidesPerView: 1.94,
			spaceBetween: 70,
		}
	},
	on: {
		init() {
			Array.from(this.slides).forEach((swiperSlide, i) => {
				const slide = swiperSlide.querySelector('.swiper-slide__wrapper');
				if (!swiperSlide.classList.contains('swiper-slide-visible')) {
					slide.style.display = 'none';
				}
			});
		},
		setTranslate() {
			Array.from(this.slides).forEach((slide,) => {
				if (slide.classList.contains('swiper-slide-visible')) {
					slide.querySelector('.swiper-slide__wrapper').style.display = '';
				}
			});
		},
		transitionEnd() {
			Array.from(this.slides).forEach((slide) => {
				if (!slide.classList.contains('swiper-slide-visible')) {
					slide.querySelector('.swiper-slide__wrapper').style.display = 'none';
				}
			});
		},
	},
});

// Slider hover
document.querySelector('.c-swiper').addEventListener('mouseenter', () => {
	categoryMainSwiper.autoplay.stop()
});
document.querySelector('.c-slide__link').addEventListener('focus', () => {
	categoryMainSwiper.autoplay.stop()
});

document.querySelector('.c-swiper').addEventListener('mouseleave', () => {
	categoryMainSwiper.autoplay.start()
});

// news-swiper
const newsSlider = new Swiper('.news-swiper', {
	loop: true,

	watchSlidesVisibility: true,
	navigation: {
		nextEl: '.news-swiper-btn-next',
		prevEl: '.news-swiper-btn-prev',
	},
	breakpoints: {// настройки для разных разрешений
		460: {
			slidesPerView: 1,
		},
		767: {
			slidesPerView: 2,
		},
		992: {
			slidesPerView: 3,

		},
	},
	on: {
		init() {
			Array.from(this.slides).forEach((swiperSlide, i) => {
				const slide = swiperSlide.querySelector('.swiper-slide__wrapper');
				if (!swiperSlide.classList.contains('swiper-slide-visible')) {
					slide.style.display = 'none';
				}
			});
		},
		setTranslate() {
			Array.from(this.slides).forEach((slide,) => {
				if (slide.classList.contains('swiper-slide-visible')) {
					slide.querySelector('.swiper-slide__wrapper').style.display = '';
				}
			});
		},
		transitionEnd() {
			Array.from(this.slides).forEach((slide) => {
				if (!slide.classList.contains('swiper-slide-visible')) {
					slide.querySelector('.swiper-slide__wrapper').style.display = 'none';
				}
			});
		},
	},
});

// Slide auto height
const newsSliderWrapper = newsSlider.wrapperEl;
[].forEach.call(newsSlider.slides, function (slide) {
	slide.style.height = "";
});
setTimeout(() => {
	[].forEach.call(newsSlider.slides, function (slide) {
		slide.style.height = newsSliderWrapper.clientHeight + "px";
	});
}, 300);